html, body {
    height: 100%;
}

body {
    font-family: Helvetica;
    margin-top: 0;
    padding: 0;
    background: linear-gradient(
        180deg, 
        #2980b9 20%, 
        #2c3e50 100%

    );
    color: #fff;
}

button {
    background: #0575E6;
    color: #fff;
    padding: 0 45px;
    border: 2px solid #fff;
    border-radius: 25px;
    height: 50px;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 1.2em;
    cursor: pointer;
    transition: .4s;
}

button:hover{
    background-color: #021B79
}